@font-face {
    font-family: 'eliteFont';
    src: url(./eurocaps.ttf) format('truetype');
    
}

.main{
    display: flex;
}

.ColorPanel {
    width: 10%;
    display: flex;
    flex-direction: column;
}

.ColorPicker {
    user-select: none;
}

.interface {
    user-select: none;
    width: 80%;
    font-family:'eliteFont';
}

svg g:hover > *:not(g) {
    stroke: white;
}

svg g:hover > *:not(g) {
    stroke-width: 1;
}