.colors-container {
    width: 100%; 
    padding: 0 10px 0; 
    margin-top: 8px;
    box-sizing: initial; 
    background: rgb(255, 255, 255); 
    border-radius: 4px; 
    box-shadow: rgba(0, 0, 0, 0.15) 0 0 0 1px, rgba(0, 0, 0, 0.15) 0 8px 16px;}

.colors-box {
    margin: 0 -10px;
    padding: 10px 0 0 10px;
    display: flex;
    flex-wrap: wrap;
    position: relative;
}

.color-container {
    width: 16px; 
    height: 16px; 
    margin: 0 10px 10px 0;
}

.color-block {
    color: #000;
    text-align: center;
    vertical-align: middle;
    font-family: 'Courier New', Courier, monospace;
    background: rgb(255, 180, 92);
    height: 100%;
    width: 100%;
    cursor: pointer;
    position: relative;
    outline: none;
    border-radius: 3px;
    box-shadow: rgba(0, 0, 0, 0.15) 0 0 0 1px inset;
}